<template>
  <div>
    <el-dialog
      :title="formModel.id ? '编辑学生' : '添加学生'"
      :visible.sync="dialogVisible"
      width="50%"
      >
        <el-form ref="form" :model="formModel" :inline="true">
          <el-form-item label="学生姓名" prop="name">
            <el-input v-model="formModel.name" placeholder="韩立"></el-input>
          </el-form-item>

          <el-form-item label="学生年龄" prop="age">
            <el-input v-model.number="formModel.age" placeholder="21"></el-input>
          </el-form-item>

          <el-form-item label="学生性别" prop="gender">
            <el-select v-model="formModel.gender" placeholder="请选择性别">
              <el-option :value="1" label="男"></el-option>
              <el-option :value="0" label="女"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="学生组号" prop="group">
            <el-select v-model.number="formModel.group" placeholder="请选择组号">
              <el-option v-for="(i, index) in 8" :key="index" :value="i" :label="i"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="期望薪资" prop="hope_salary">
            <el-input v-model.number="formModel.hope_salary" placeholder="23200"></el-input>
          </el-form-item>

          <el-form-item label="就业薪资" prop="salary">
            <el-input v-model.number="formModel.salary" placeholder="5800"></el-input>
          </el-form-item>

          <el-form-item label="所在省份" prop="province">
            <el-select @input="getCity" v-model="formModel.province" placeholder="-所在省份-">
              <el-option  v-for="i in provincesList" :key="i.id"
              :value="i.proName"
              :label="i.proName">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="所在城市" prop="city" >
            <el-select @input="getArea" v-model="formModel.city" placeholder="-所在城市-">
              <el-option v-for="i in cityList" :key="i.id"
              :value="i.cityName"
              :label="i.cityName">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="所在县(区)" prop="area" >
            <el-select v-model="formModel.area" placeholder="-所在县(区)-">
              <el-option v-for="i in areaList" :key="i.id"
              :value="i.areaName"
              :label="i.areaName">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getProvincesList, gerCityList, gerAreaList } from '@/api/address'
import { addNewStudent, editStudent } from '@/api/student'
export default {
  data () {
    return {
      dialogVisible: false,
      defaultForm: {
        name: '',
        age: '',
        gender: '',
        province: '',
        city: '',
        area: '',
        hope_salary: '',
        salary: '',
        group: ''
      },
      formModel: {
        name: '',
        age: '',
        gender: '',
        province: '',
        city: '',
        area: '',
        hope_salary: '',
        salary: '',
        group: ''
      },
      provincesList: [],
      cityList: [],
      areaList: []
    }
  },
  methods: {
    open (row) {
      if (row.id) {
        // 编辑
        this.dialogVisible = true
        this.formModel = row
      } else {
        // 添加
        this.dialogVisible = true
      }
    },
    // 获取市
    async getCity (val) {
      const proId = this.provincesList.find(i => i.proName === val).proId
      const { data: { list } } = await gerCityList(proId)
      this.cityList = list
    },
    // 获取区县
    async getArea (val) {
      const cityId = this.cityList.find(i => i.cityName === val).cityId
      const { data: { list } } = await gerAreaList(cityId)
      this.areaList = list
    },

    // 提交表单
    async onSubmit () {
      if (this.formModel.id) {
        // 编辑
        const res = await editStudent(this.formModel, this.formModel.id)
        this.dialogVisible = false
        this.$emit('success', 1)
      } else {
        // 添加
        const { data } = await addNewStudent(this.formModel)
        // console.log(data)
        this.$emit('success', 0)
        this.dialogVisible = false
      }
    }
  },
  watch: {
    dialogVisible (val) {
      if (!val) {
        this.formModel = { ...this.defaultForm }
        // this.$destroy()
      }
    }
  },
  async created () {
    // 获取省
    const { data } = await getProvincesList()
    this.provincesList = data.list
  }
}
</script>

<style></style>
