<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <Headercomponer title="学生列表">
      <!-- 标题及操作区 -->
      <div slot="extra">
        <el-button @click="handleAdd" type="success">+添加学生</el-button>
      </div>

      <!-- 主要内容 -->
      <div>
        <el-form :inline="true">
          <el-form-item>
            <el-input v-model.trim="vualeS"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="onSearch('s')" type="success">搜索</el-button>
            <el-button @click="onSearch('r')" type="primary">重置</el-button>
          </el-form-item>
        </el-form>
        <!-- 学生列表 -->
        <el-table :data="pagedData" style="width: 100%"
        v-loading="loading" max-height="900px">
          <el-table-column
            prop="id"
            label="学生ID"
            width="auto">
          </el-table-column>

          <el-table-column
            prop="name"
            label="学生姓名"
            width="auto">
          </el-table-column>

          <el-table-column
            prop="age"
            label="学生年龄"
            width="auto">
          </el-table-column>

          <el-table-column
            prop=""
            label="学生性别"
            width="auto">
            <template #default="{row}">{{ row.gender === 1 ? '男' : '女' }}</template>
          </el-table-column>

          <el-table-column
            prop="group"
            label="学生组号"
            width="auto">
          </el-table-column>

          <el-table-column
            prop="hope_salary"
            label="期望薪资"
            width="auto">
          </el-table-column>

          <el-table-column
            prop="salary"
            label="就业薪资"
            width="auto">
          </el-table-column>

          <el-table-column
            prop=""
            label="籍贯"
            width="auto">
            <template #default="{row}">
              {{ row.province + '-' + row.city + '-' + row.area }}
            </template>
          </el-table-column>

          <el-table-column
            prop=""
            label="籍贯"
            width="auto">
            <template #default="{row}">
              {{ new Date(row.createdAt).toLocaleString() }}
            </template>
          </el-table-column>

          <el-table-column
          fixed="right"
          label="操作"
          width="110">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="success" icon="el-icon-edit" circle></el-button>
            <el-button @click="handleDelt(scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
          </template>
        </el-table-column>
        </el-table>

        <!-- 分页 -->
        <el-pagination
          @size-change="handleChangeLamit"
          @current-change="handleChangePage"
          :current-page="page"
          :page-sizes="[10, 15, 25, 35]"
          :page-size="10"
          layout="total, sizes, prev, pager, next"
          :total="total">
        </el-pagination>

        <!-- 弹窗 -->
        <studentAlter @success="onSuccess" ref="sAlter"></studentAlter>
      </div>
    </Headercomponer>
  </div>
</template>

<script>
import { getStudentList, deleteStudent } from '@/api/student'
import Headercomponer from '@/components/headercomponer.vue'
import studentAlter from '@/components/studentAlter.vue'
import { Message } from 'element-ui'
export default {
  data () {
    return {
      loading: false,
      studentList: [],
      page: 1,
      lamit: 10,
      vualeS: ''
    }
  },
  methods: {
    // 获取学生列表方法
    async getStudentList () {
      this.loading = true
      const { data: res } = await getStudentList()
      this.studentList = res.data
      this.loading = false
    },
    // 页码改变时
    handleChangePage (page) {
      this.page = page
    },
    // 显示条数改变时
    handleChangeLamit (lamit) {
      this.page = 1
      this.lamit = lamit
    },
    // 添加
    handleAdd () {
      this.$refs.sAlter.open({})
    },
    // 编辑
    handleEdit (row) {
      this.$refs.sAlter.open(row)
    },
    // 删除
    handleDelt (row) {
      this.$confirm('您确定需要永久删除该项么？', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await deleteStudent(row.id)
        Message.success('删除成功')
        this.getStudentList()
      }).catch(() => {})
    },

    // 编辑 或 添加 成功的回调
    onSuccess () {
      // 重新渲染
      Message.success('操作成功')
      this.getStudentList()
    },

    // 搜索
    onSearch (type) {
      if (type === 's') {
        // 搜索
        if (this.vualeS === '') {
          this.getStudentList()
          return
        }
        const i = this.studentList.find(i => i.name === this.vualeS)
        if (i === undefined) {
          this.studentList = []
          return
        }
        this.studentList = []
        this.studentList.push(i)
      }
      if (type === 'r') {
        // 重置
        this.getStudentList()
      }
    }

  },
  computed: {
    // 分页
    pagedData () {
      const start = (this.page - 1) * this.lamit
      const end = start + this.lamit
      return this.studentList.slice(start, end)
    },
    // 总条数
    total () {
      return this.studentList.length
    }
  },

  created () {
    this.$store.commit('tag/selecMenu', {
      path: this.$route.path,
      label: '学生列表'
    })

    // 调用获取学生列表方法
    this.getStudentList()
  },
  components: {
    Headercomponer,
    studentAlter
  }
}
</script>

<style lang="less" scoped></style>
