// 省市区
import $http from '@/utile/$address.js'

// 获取省
export const getProvincesList = () => $http.get('/api/provincesList')

// 获取市
export const gerCityList = (proId) => $http.get('/api/cityList', {
  params: {
    proId
  }
})

// 获取县
export const gerAreaList = (cityId) => $http.get('/api/areaList', {
  params: {
    cityId
  }
})
